
@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&family=MuseoModerno:wght@100&family=Raleway:wght@300&family=Saira+Stencil+One&family=Tourney:wght@100&family=Zen+Kurenaido&family=Zen+Tokyo+Zoo&display=swap');

/* CSS rules to specify families

font-family: 'Comfortaa', cursive;
font-family: 'MuseoModerno', cursive;
font-family: 'Raleway', sans-serif;
font-family: 'Saira Stencil One', cursive;
font-family: 'Tourney', cursive;
font-family: 'Zen Kurenaido', sans-serif;
font-family: 'Zen Tokyo Zoo', cursive; */

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body {
  /* font-family: Zen Tokyo Zoo, cursive; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image: url('https://i.imgur.com/ZVOMEyI.jpg');
  background-repeat: no-repeat; */

  font-weight: 400;
  color: rgb(9, 69, 9);
}

main {

  /* background-image: url(''); */
  /* background-repeat: no-repeat;
  background-size: cover; */
  width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
  background-color: transparent;
}

ul,
ol {
  list-style: none;
  /* font-family: 'Zen Kurenaido', sans-serif; */
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
 
  font-size: 100%;
  font-weight: normal;
  font-weight: normal;
}

img,
video {
  display: block;
  max-width: 100%;
}

.btn {
 
  border: 4px double rgb(37, 176, 44);
  padding: 10px 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  font-weight: 400;
  outline: none;
}

.PrimaryBtn {
  background: rgba(247, 248, 247, 0.67);
  color: #0a0a0a;
}

.PrimaryBtn:hover {
  background: rgb(30, 195, 200);
  color: #010606;
}

.SecondarBtn:hover {
  background: rgb(30, 195, 200);
  color: #fff;
}

.btn2 {
  margin-right: 1rem;
}

.Container {
  width: 100%;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto;
}

.BigCard {
  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-right: 2rem;
  padding-left: 2rem;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 5px 10px;
  /* box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05); */
}

.AboutBio,
.tagline2 {
  text-align: center;
  max-width: 650px;
  margin: 0 auto;
  color: #151418;
  line-height: 1.6;
}

.tagline2 {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.SectionTitle {
  font-size: 28px;
  color: #151418;
  font-weight: 700;
  
  margin-bottom: 3.5rem;
}
.ProjectWrapper {
  margin-top: -1rem;
}

/* ------------------------------------ Media for Container */
@media (min-width: 576px) {
  .Container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .Container {
    max-width: 720px;
  }
  .ProjectWrapper {
    margin-top: -5rem;
  }
}
@media (min-width: 992px) {
  .Container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .Container {
    max-width: 1000px;
  }
}
